<template>
  <div class="Component-TablaDeModelo">
    <div class="un_espaciado">
      <button class="un_control" v-on:click="goToAddPage">Crear {{ internal_model_genre === "masculino" ? "nuevo" : "nueva" }} {{ $utils.fromCamelToCapital(internal_model).toLowerCase() }}</button>
    </div>
    <div class="un_espaciado_sin_arriba_ni_abajo">
      <input type="text" class="un_control el_buscador" v-model="internal_search_brute" :placeholder="'Buscador de ' + $utils.fromCamelToCapital(internal_model).toLowerCase() + ''" :title="'Buscador de ' + $utils.fromCamelToCapital(internal_model).toLowerCase() + ''" />
    </div>
    <div class="un_espaciado">
      <div class="un_visor_de_tabla" v-if="internal_data && internal_data.length && internal_model_columns">
        <table class="una_tabla_de_datos">
          <thead>
            <tr>
              <th class="ancho_minimo"></th>
              <th class="ancho_minimo">ID</th>
              <template v-for="(prop, propIndex) in internal_model_columns">
                <th v-if="propIndex !== 'id'" v-bind:key="'table-modelo-' + internal_model + '-header-' + propIndex">
                  {{ $utils.fromCamelToCapital(propIndex) }}
                </th>
              </template>
              <th class="ancho_minimo"></th>
            </tr>
          </thead>
          <tbody>
            <template v-for="(row, rowIndex) in internal_data">
              <tr v-bind:key="'model-' + internal_model + '-row-' + rowIndex">
                <td class="ancho_minimo">
                  <button class="un_control" type="button" v-on:click="() => goToEditPage(row)">Editar</button>
                </td>
                <td class="ancho_minimo">
                  {{ row.id }}
                </td>
                <td v-for="(prop, propIndex) in internal_model_columns" v-bind:key="'model-' + internal_model + '-row-' + rowIndex + '-prop-' + propIndex">
                  <span class="un_contenido_de_celda" v-if="prop.startsWith('list:') && Array.isArray(row[propIndex])" :title="row[propIndex].map(v => typeof v === 'object' ? v.nombre : v).join(', ')">
                  {{ row[propIndex].map(v => typeof v === "object" ? v.nombre : v).join(", ") }}
                  </span>
                  <span class="un_contenido_de_celda" v-else :title="row[propIndex]">
                  {{ row[propIndex] }}
                  </span>
                </td>
                <td class="ancho_minimo">
                  <button class="un_control" type="button" v-on:click="() => goToDeleteDialog(row)">Eliminar</button>
                </td>
              </tr>
            </template>
          </tbody>
        </table>
      </div>
      <div class="el_mensaje_de_carga" v-else-if="typeof internal_data === 'undefined'">
        <span class="">Cargando datos... Por favor, espere...</span>
      </div>
      <div class="el_mensaje_de_carga" v-else-if="internal_search_brute === ''">
        <span class=""
          >Actualmente no hay {{ internal_model_genre === "masculino" ? "ningn" : "ninguna" }} {{ $utils.fromCamelToCapital(internal_model) }} registrad{{ internal_model_genre === "masculino" ? "o" : "a" }}. Cree un{{ internal_model_genre === "masculino" ? "o" : "a" }} nuev{{
            internal_model_genre === "masculino" ? "o" : "a"
          }}
          <a href="#" v-on:click="goToAddPage">aqu</a>.</span
        >
      </div>
      <div class="el_mensaje_de_carga" v-else>
        <span class=""
          >No se encontraron registros de <b>{{ internal_model }}</b> con los actuales parmetros de bsqueda.</span
        >
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "TablaDeModelo",
  props: {
    root: {
      type: Object,
      required: true,
    },
    model: {
      type: String,
      required: true,
    },
  },
  data() {
    const hasModelo = this.model in this.$db.schema.modelos;
    if (!hasModelo) {
      console.error(this.$db.schema.modelos, this.model);
      throw new Error("Model not found in schema: " + this.model + " in " + Object.keys(this.$db.schema.modelos).join(", "));
    }
    return {
      internal_model: this.model,
      internal_model_genre: this.$db.schema.modelos[this.model].genero,
      internal_model_columns: this.$db.schema.modelos[this.model].columnas,
      internal_data: undefined,
      internal_search_brute: "",
      internal_search: "",
      internal_where: [],
      internal_sort: [],
      internal_page_error: undefined,
    };
  },
  methods: {
    goToAddPage() {
      const addPage = this.$utils.getAddPageFromModel(this.internal_model);
      this.root.goToPage(addPage);
    },
    goToEditPage(row) {
      const editPage = this.$utils.getEditPageFromModel(this.internal_model);
      this.root.goToPage(editPage, { value: row });
    },
    goToDeleteDialog(row) {
      const isConfirmed = window.confirm("Ests seguro de eliminar el elemento " + this.internal_model + ":" + row.id + "?");
      if (!isConfirmed) {
        return;
      }
      this.$db
        .delete(this.internal_model, row.id)
        .then((data) => {
          this.$noop(data);
          this.refreshData();
          return data;
        })
        .catch((error) => {
          this.internal_page_error = error;
          return error;
        });
    },
    refreshData() {
      const hasSearch = !!this.internal_search;
      const hasWhere = !!this.internal_where;
      const hasSort = Array.isArray(this.internal_sort) && this.internal_sort.length;
      const searchText = this.internal_search;
      const whereFilters = this.internal_where;
      return this.$db
        .select(this.internal_model, {
          filter: (row) => {
            if (!hasSearch && !hasWhere) {
              return true;
            }
            const props = Object.keys(row);
            let hasMatchedSearch = false;
            if (hasSearch) {
              SearchTextLoop: for (let indexProp = 0; indexProp < props.length; indexProp++) {
                const prop = props[indexProp];
                const value = row[prop];
                if (hasSearch) {
                  const position = ("" + value).indexOf(searchText);
                  if (position !== -1) {
                    hasMatchedSearch = true;
                    break SearchTextLoop;
                  }
                }
              }
            } else {
              hasMatchedSearch = true;
            }
            let hasMatchedWhere = true;
            if (hasWhere) {
              SearchWhereLoop: for (let indexWhere = 0; indexWhere < whereFilters.length; indexWhere++) {
                const whereFilter = whereFilters[indexWhere];
                this.$noop(whereFilter);
                const { column, operation, parameters } = whereFilter;
                const rowColumn = "" + (((typeof row[column] === "undefined") || (row[column] === null)) ? "" : row[column]);
                let logicalResult = false;
                if (operation === "<") {
                  logicalResult = rowColumn < parameters;
                } else if (operation === "<=") {
                  logicalResult = rowColumn <= parameters;
                } else if (operation === ">") {
                  logicalResult = rowColumn > parameters;
                } else if (operation === ">=") {
                  logicalResult = rowColumn >= parameters;
                } else if (operation === "=") {
                  logicalResult = rowColumn === parameters;
                } else if (operation === "!=") {
                  logicalResult = rowColumn !== parameters;
                } else if (operation === "in") {
                  logicalResult = parameters.split(",").indexOf(rowColumn) !== -1;
                } else if (operation === "not in") {
                  logicalResult = parameters.split(",").indexOf(rowColumn) === -1;
                } else if (operation === "has") {
                  logicalResult = rowColumn.indexOf(parameters) !== -1;
                } else if (operation === "has not") {
                  logicalResult = rowColumn.indexOf(parameters) === -1;
                }
                if (logicalResult === false) {
                  hasMatchedWhere = false;
                  break SearchWhereLoop;
                }
              }
            }
            return hasMatchedSearch && hasMatchedWhere;
          },
        })
        .then((dataPreset) => {
          let data = dataPreset;
          if (hasSort) {
            data = dataPreset.sort((a, b) => {
              for (let indexSort = 0; indexSort < this.internal_sort.length; indexSort++) {
                const internal_sort_rule = this.internal_sort[indexSort];
                const { sense, column } = internal_sort_rule;
                if (!(column in a)) {
                  return -1;
                } else if (!(column in b)) {
                  return -1;
                }
                const isAscendant = sense === "+";
                const columnA = ((typeof a[column] === "undefined") || (a[column] === null)) ? "" : a[column];
                const columnB = ((typeof b[column] === "undefined") || (b[column] === null)) ? "" : b[column];
                if (columnA < columnB) {
                  return isAscendant ? -1 : 1;
                } else if (columnA > columnB) {
                  return isAscendant ? 1 : -1;
                }
              }
              return -1;
            });
          }
          this.internal_data = data;
          return data;
        })
        .catch((error) => {
          this.internal_page_error = error;
        });
    },
    refreshSearch(v) {
      const ast = this.$utils.parseSearch(v);
      this.internal_search = ast.search;
      this.internal_where = ast.where;
      this.internal_sort = ast.sort;
      this.refreshData();
    },
  },
  watch: {
    internal_search_brute(v) {
      this.refreshSearch(v);
    },
  },
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.refreshData();
    window.tdm = this;
  },
  updated() {},
  destroyed() {},
};
</script>
